/************************************************
 *
 *Stylesheet: Main Stylesheet
 *
 ************************************************* */

/*********************
 *GENERAL STYLES
 ********************* */

@import url('https://fonts.googleapis.com/css?family=Lato:400,700|Raleway');

$warmclay: #817066;
$green: #B0BC3F;
$blue: #5E8BAD;
$darkclay: #695a52;

@mixin cssTransition($secs) {
  -webkit-transition: all $secs ease-in-out;
  -moz-transition: all $secs ease-in-out;
  -o-transition: all $secs ease-in-out;
  -ms-transition: all $secs ease-in-out;
  transition: all $secs ease-in-out; }

@mixin shadowTransition($secs) {
  -webkit-transition: box-shadow $secs ease-in-out;
  -moz-transition: box-shadow $secs ease-in-out;
  -o-transition: box-shadow $secs ease-in-out;
  -ms-transition: box-shadow $secs ease-in-out;
  transition: box-shadow $secs ease-in-out; }

body {
  font-family: 'Raleway', sans-serif; }

.wpcf7-submit {
  background-color: $warmclay;
  color: #fff;
  text-transform: uppercase;
  padding: 0.85em 1em;
  font-size: 1rem;
  font-family: 'Raleway', sans-serif;
  border: 0;
  cursor: pointer;
  &:hover {
    background: #8c7e75; } }

.button.contact {
  background-color: #a2a2a2;
  text-transform: uppercase;
  &:hover {
    background-color: #8e8e8e; } }



/*********************
 *LINK STYLES
 ********************* */
a {
  @include cssTransition(.3s); }

a, a:visited {
  // Mobile tap color
  &:link {
    //Highlight links on iOS, Similar to :hover for mobile devices.
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3); } }

/*********************
 *H1, H2, H3, H4, H5 P STYLES
 ********************* */
h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {
  font-family: 'Lato', sans-serif;
  a {
    text-decoration: none; } }

h1, .h1 {}

h2, .h2 {}

h3, .h3 {}

h4, .h4 {}

h5, .h5 {}

p {
  font-size: 1.5rem;
  line-height: 1.95rem; }

/*********************
 *HEADER STYLES
 ********************* */
.header {
  ul.off-canvas-list li {
    list-style: none; } }

.logo a {
  width: 300px; }


/*********************
 *NAVIGATION STYLES
 ********************* */
.top-bar {
  .title-area {
    z-index: 1; }
  .top-bar ul {
    background: transparent; } }

ul#menu-main-1 {
  margin-top: 2rem;
  li.donate a {
    background-color: $warmclay;
    color: #fff;
    border-radius: 3px;
    text-transform: uppercase;
    background: $warmclay url('/wp-content/themes/yoga-mala-foundation/assets/images/donate-symbol.png') no-repeat 8px center;
    background-size: 33px;
    padding-left: 47px;
    &:hover {
      background: #8c7e75 url('/wp-content/themes/yoga-mala-foundation/assets/images/donate-symbol.png') no-repeat 8px center;
      background-size: 33px;
