  a {
    color: $warmclay;
    @include cssTransition(.2s);
    &:hover {
      opacity: 0.7; } } }

//
// Mobile Menu
//

.show-for-mobile-down {
  display: none; }

.off-canvas {
  ul {
    margin-left: 0;
    li {
      a {
        border-bottom: 0px;
        color: $warmclay;
        &:hover {
          opacity: .7; } } } } }

.menu-icon:after {
