  background: #d8d8d8;
  box-shadow: 0 6px 0 #D8D8D8, 0 12px 0 #D8D8D8;
  &:hover {
    height: 3px;
    background: #C1C1C1;
    box-shadow: 0 6px 0 #C1C1C1, 0 12px 0 #C1C1C1; } }



